.container {
  background: linear-gradient(#121212, #212b46);
  color: #fff;
  height: 100vh;
  display: grid;
  place-items: center;
  grid-template-rows: 1fr 0.5fr 1.5fr;
}

.title {
  align-self: flex-end;
  font-size: 40px;
  text-align: center;
}

.input-container {
  background-color: rgba(255, 255, 255, 0.2);
  display: flex;
  gap: 5px;
  align-items: center;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 1px 3px 8px rgba(0,0,0,0.5);
}

.input-container input {
  background-color: transparent;
  color: #fff;
  font-size: 18px;
  padding: 5px 10px;
  border: none;
  outline: none;
}

.btn-search {
  background: transparent;
  color: #fff;
  display: flex;
  align-items: center;
  font-size: 24px;
  border: none;
  cursor: pointer;
}

.outcome {
  align-self: flex-start;
  background-color: #fff;
  color: #121212;
  width: 450px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 8px;
  text-align: center;
}

.outcome h3 {
  font-size: 24px;
  margin: 16px 0;
}

.outcome p {
  margin-bottom: 12px;
}

@media only screen and (max-width: 460px) {
  .outcome {
    width: 100%;
  }
}

@media only screen and (max-width: 410px) {
  .title {
    font-size: 32px;
  }

  .input-container input{
    font-size: 14px;
  }
}

@media only screen and (max-width: 340px) {
  .input-container {
    width: 100%;
    padding: 5px;
  }

  .input-container input{
    width: 100%;
  }

  .btn-search {
    font-size: 20px;
  }
}
